import { defineComponent } from 'vue';
import { before } from '@/BabylonExamples/before';
import { Work } from '@/BabylonExamples/Work';

export default defineComponent({
  name: 'BabylonExamples',

  data() {
    return {
      loaded: false,
    };
  },
  mounted(){
    const canvas = document.querySelector("canvas") as HTMLCanvasElement;
    const loadingBar = document.getElementById("loadingBar") as HTMLElement; //그냥 껐다 켜니까 됨 아니 안됨
    const percentLoaded = document.getElementById("percentLoaded") as HTMLElement;
    const loader = document.getElementById("loader") as HTMLElement;

    const gamestartBG = document.getElementById("gamestartBG") as HTMLElement;
    const tspudlogo = document.getElementById("tspudlogo") as HTMLElement;

    new before();

    new Work(
      canvas,
      this.setLoaded, //걍 매개변수 중 함수 빼먹어서 오류난거였음..
      loadingBar,
      percentLoaded,
      loader,
      gamestartBG,
      tspudlogo,
      );
  },
  methods: {
    setLoaded() {
      this.loaded = true;
    },
  },
  
});
