
export class before {

  constructor() {
    const yesbtn = document.getElementById("yes");
    yesbtn.addEventListener("click", (e:Event) => this.getStart(1));
    const nobtn = document.getElementById("no");
    nobtn.addEventListener("click", (e:Event) => this.getStart(2));
    const continue67 = document.getElementById("continue");
    continue67.addEventListener("click", (e:Event) => this.getStart(4));
    const btn = document.getElementById("gamestart");
    btn.addEventListener("click", (e:Event) => this.getStart(3));
  }
  getStart(n:number){
    // button click handler
    console.log(n);
    if (n === 1) {
      document.getElementById('chooseBG').style.display = 'none';
      document.getElementById('chooseNoBG').style.display = 'none';
      document.getElementById('gamestartBG').style.display = 'flex';
    }
    if (n === 2) {
      document.getElementById('chooseBG').style.display = 'none';
      document.getElementById('gamestartBG').style.display = 'none';
      document.getElementById('chooseNoBG').style.display = 'flex';
    }
    if (n === 4) {
      window.location.href = "https://store.steampowered.com/app/1703340/The_Stanley_Parable_Ultra_Deluxe/";
      document.getElementById('chooseBG').style.display = 'none';
      document.getElementById('gamestartBG').style.display = 'none';
      document.getElementById('chooseNoBG').style.display = 'flex'; 
    }
    if (n === 3) {
      console.log('buttonstart');
      document.getElementById('chooseBG').style.display = 'none';
      document.getElementById('chooseNoBG').style.display = 'none';
      document.getElementById('gamestartBG').style.display = 'none';
    //   setTimeout(() => {
    //   document.getElementById('tspudlogo').style.display = "none";
    //   document.getElementById('crowslogo').style.display = "none";
    // }, 2000);
    }


  }
}