//로딩클래스
class screenloading implements ILoadingScreen {

  constructor(
    private loadingBar: HTMLElement,
    private percentLoaded: HTMLElement,
    private loader: HTMLElement,

    private tspudlogo: HTMLElement,
  ) {
  }

  //What happens when loading starts  
  displayLoadingUI(): void {
    this.loadingBar.style.width = "0%";
    this.percentLoaded.innerText = "0%";
  }

  //What happens when loading stops  
  hideLoadingUI(): void {
    setTimeout(() => {
      this.tspudlogo.style.display = "none";
    }, 3500);

    //새로고침할 때마다 바꿔주기 왜 else는 안되는지 모르겠음
    if (this.loader.id === "loader") {
      this.loader.id = "loaded"; //로딩 다되면 로딩중에서 로딩끝 아이디로 바꾸기
    }
    if (this.loader.id === "loaded") {
      this.loader.id = "loader";
    }       
    
    setTimeout(() => {
      this.loader.style.display = "none";
    }, 1000);

  }

  updateLoadStatus(status: number): void {
    this.loadingBar.style.width = `${status}%`;
    this.percentLoaded.innerText = `${status}%`;
  }

  //default loader support. Optional!
  loadingUIBackgroundColor: string;
  loadingUIText: string;
}


