    this.waitingsoundcollection = [Waitbuttonvoice0, Waitbuttonvoice1, Waitbuttonvoice2, Waitbuttonvoice3, Waitbuttonvoice4, Waitbuttonvoice5, Waitbuttonvoice6, Waitbuttonvoice7];

    //버튼 목소리 모음
