  CreateKeyAudio() : void {

    //배경음악
    const backgroundMusic = new Sound("backgroundMusic", './audio/sfx/amb.wav', this.scene, null, {
      volume: 0.8, //볼륨
      autoplay: true,
      loop: true
    });

    //.2로 적으면 못알아먹음
    
    //backgroundMusic.setVolume(0.2, 30); //3초동안 페이드인

    //발자국
    const step = new Sound("step", "./audio/sfx/step.wav", this.scene, null, {
      volume: 1, //볼륨
      autoplay: false,
    }); //이을것!

    let isKeyDown = false;

    //키보드
    window.addEventListener("keydown", function(evt: KeyboardEvent) {
        if (!isKeyDown) {
          isKeyDown = true;
          step.loop = true;
          step.play();
        }
    });

    window.addEventListener("keyup", function(evt: KeyboardEvent) {
        if (isKeyDown) {
          isKeyDown = false;
          step.loop = false;
          step.stop();
        }
    });

  }

  CreatePositionAction () : void {
    //문소리
    const dooropensound = new Sound("dooropensound", './audio/sfx/open.wav', this.scene, null, {
      volume: 0.7, //볼륨
      autoplay: false,
      loop: false
    });

    const knocksound = new Sound("knocksound", './audio/sfx/knock.mp3', this.scene, null, {
      volume: 0.4, //볼륨
      autoplay: false,
      loop: false
    });

    const doorclosesound = new Sound("doorclosesound", './audio/sfx/close.wav', this.scene, null, {
      volume: 0.6, //볼륨
      autoplay: false,
      loop: false
    });

    // let dooropen = true;
    this.knock = 0;

    //감지장소
    this.place.position = new Vector3(-0.5, 19.5, 54); //카메라 높이 + 박스 높이 + 물체 높이 -2
    this.place.checkCollisions = true;

    //나가는 길
    this.wayout.actionManager = new ActionManager(this.scene);

    //문 닫는 속도
    let closeangle = 0.04;
    //문 여는 속도
    let openangle = -0.04;
    
    
    //액션 모음
    //닫는 액션
    const closeaction = new ExecuteCodeAction(
        {
            trigger: ActionManager.OnIntersectionEnterTrigger, 
            parameter: { 
                mesh: this.place
            }
        },
        () => {
            console.log('intersect'); //카메라 크기와 위치 중요
            this.scene.actionManager.registerAction(new ExecuteCodeAction(
              {
                trigger: ActionManager.OnEveryFrameTrigger, //중요
                parameter: { 
                    // mesh: this.door
                    mesh: this.door
                }
              }, () => {

                //문 닫힘
                this.closecount++;
                  this.door.rotate(new Vector3(0, -1, 0), closeangle);
                  if (this.closecount === 40) {
                    //문 닫히는 액션
                    closeangle = 0;
                    this.dooropen = false;

                    //목소리
                    if (!this.play) {
                      this.play = true; 
                      doorclosesound.play();
                      this.doorvoicecollection[0].play();
                    }
                    this.doorvoicecollection[0].onended = () => {
                      this.play = false;
                      //this.doorPlay = true;
                    }
                  }
              }
            ));
        }
    );

    //여는 액션
    const openaction = new ExecuteCodeAction(
      {
        trigger: ActionManager.OnEveryFrameTrigger, //중요
        parameter: { 
            // mesh: this.door
            mesh: this.door
        }
      }, () => {

        //문 열림
        this.opencount++;
          this.door.rotate(new Vector3(0, -1, 0), openangle);
          if (this.opencount === 40) {
            //문 열리는 액션
            openangle = 0;

            //목소리
            if (!this.play) {
              this.play = true;
              this.doorvoicecollection[3].play();
            }
            this.doorvoicecollection[3].onended = () => {
              this.play = false;
              this.dooropenvoiceend = true;

            }
          }
    });

    //땅에 닿았을때
    const out = new ExecuteCodeAction(
      {
          trigger: ActionManager.OnIntersectionEnterTrigger, 
          parameter: { 
              mesh: this.ground
          }
      },
      () => {
        if (this.Icount === 0 && this.buttonclick === 0 && this.dooropenvoiceend == true) {
          //목소리
              this.groundvoice.play();    
              this.Icount = 1;    
        }     
          }
    );

    const doorActionArray = [closeaction, openaction];

    //특정 위치 도달하면 문 닫힘
    this.cameraholder.actionManager.registerAction(doorActionArray[0]);

    //door 에 클릭액션 주기
    this.door.actionManager.registerAction(
      new ExecuteCodeAction(
          ActionManager.OnPickTrigger, () => {
            console.log('knock', this.knock);
            //this.doorPlay = true;
            
            //문 닫혔을 때
            //1번
            if (this.dooropen != true) {

              // if (this.knock === 0) {
              //   if (!this.play) {
              //     this.play = true;
              //     knocksound.play();                  
              //     this.doorvoicecollection[1].play();
              //   }
              //   this.doorvoicecollection[1].onended = () => {
              //     this.play = false;

              //     //노크하기
              //     this.knock++;
              //     console.log(this.knock);
              //   }
              // }

                //2번
                // if (this.knock === 1) {
                //   //목소리
                //   if (!this.play) {
                //     this.play = true;
                //     knocksound.play();
                //     this.doorvoicecollection[2].play();
                //   }
                //   this.doorvoicecollection[2].onended = () => {
                //     this.play = false;

                //     //노크하기
                //     this.knock++;
                //   }

                // }

                //3번
                //if (this.knock === 2) {
                if (this.knock === 0) {
                  //문열기
                  this.dooropen = true;
                  knocksound.play();
                  dooropensound.play();
                  //angle = -0.01; //백터가 아니라 회전 방향을 반대로 바꾸면 되는 거였음
                  this.scene.actionManager.registerAction(doorActionArray[1]);
                  console.log('ground', this.Icount);
                  if (this.Icount === 0) {
                    this.cameraholder.actionManager.registerAction(out)
                  }    

                  //노크하기
                  this.knock++;
                }

            }
    }));

  }

